<template>
  <div class="login">
    <!-- 登录框主体 -->
    <div class="login-container">
      <img class="logo" src="@/assets/logo.png" alt="广汽丰田logo">
      <p class="title">积分服务中心</p>

      <p class="tips">*GTMC用户请使用员工号登录，非GTMC用户请使用邮箱登录</p>
      <el-form ref="loginForm" :model="loginForm" :rules="rules">
        <el-form-item prop="account">
          <el-input
            v-model="loginForm.account"
            placeholder="员工号 / 邮箱"
            clearable
            ref="account"
          ></el-input>
        </el-form-item>

        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            type="password"
            placeholder="密码"
            clearable
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button
            class="login-btn"
            type="danger"
            :loading="isLoggingIn"
            native-type="submit"
            @click.prevent="onSubmit"
          >{{ loginBtnText }}</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  name: 'Login',
  data () {
    return {
      loginForm: { // 登录提交的信息
        account: 'admin',
        password: 'admin'
      },
      isLoggingIn: false, // 是否提交中
      loginBtnText: '登 录', // 提交按钮提示文本
      rules: {
        account: [
          { required: true, message: '请输入员工号 / 邮箱', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.$refs.account.focus()
    })
  },
  methods: {
    ...mapMutations(['setAccountInfo']),

    // 提交前验证
    onSubmit () {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.onLogin()
        } else {
          return false
        }
      })
    },

    // 提交登录
    onLogin () {
      this.isLoggingIn = true
      this.loginBtnText = '登录中...'
      this.$http.serviceLogin(this.loginForm)
        .then(res => {
          this.saveAccountInfo(res)
          this.$message.success('登陆成功')
          this.$router.push('/deptConsole').catch(() => {}) // vue-router v3.1.0 出现的问题，具体可查看github更新log：https://github.com/vuejs/vue-router/releases
        })
        .catch(error => {
          if (!error.code && error.name) Promise.reject(error)
          else this.$alert(error.message, '登录失败', { type: 'error' })
        })
        .finally(() => {
          this.isLoggingIn = false
          this.loginBtnText = '登 录'
        })
    },

    // 保存用户信息
    // sessionStorage保存一份，以防刷新vuex数据丢失
    // vuex保存一份，方便信息读取
    saveAccountInfo (data) {
      sessionStorage.setItem('accountInfo', JSON.stringify(data))
      this.setAccountInfo(data)
    }
  }
}
</script>

