  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -5%; // 微调整，向上偏移5%，百分比的参照为容器的宽度
  height: 100%;
  background-color: #fff;

  .login-container {
    width: 350px;

    .logo {
      display: block;
      width: 100%;
    }

    .title {
      margin-top: 5px;
      margin-bottom: 30px;
      text-align: right;
      font-size: 16px;
      font-weight: bold;
      color: #606266;
    }

    .tips {
      font-size: 12px;
      color: #909399;
    }

    .login-btn {
      width: 100%;
    }
  }
}
</style>
